import React, { Component } from 'react';
import { Route, Switch, withRouter, Redirect } from 'react-router-dom';
import { connect } from 'react-redux';
import asyncComponent from './hoc/asyncComponent/asyncComponent';
import Layout from './hoc/Layout/Layout';
import LandingPage from './containers/LandingPage/LandingPage';
import Logout from './containers/Logout/Logout';

import * as actions from './store/actions/index';

import './index.css';

const asyncAuth = asyncComponent(() => {
  return import('./containers/AuthPage/AuthPage');
});

const asyncRegister = asyncComponent(() => {
  return import('./containers/RegisterPage/RegisterPage');
});

const asyncDashboard = asyncComponent(() => {
  return import('./components/Dashboard/Dashboard');
});

const asyncPlayground = asyncComponent(() => {
  return import('./containers/Playground/Playground');
});

const asyncProfile = asyncComponent(() => {
  return import('./containers/ProfilePage/ProfilePage');
});

class App extends Component {

  componentDidMount () {
    this.props.onTryAutoSignup();
  }

  render () {
    let routes = (
      <Switch>
        <Route path='/auth/signup' component={asyncRegister} />
        <Route path="/auth/signin" component={asyncAuth} />
        <Route path="/" exact component={LandingPage} />
        <Redirect to="/" />
      </Switch>
    );

    if ( this.props.isAuthenticated ) {
      routes = (
        <Switch>
          <Route path="/dashboard" component={asyncDashboard} />
          <Route path="/playground" component={asyncPlayground} />
          <Route path="/profile" component={asyncProfile} />
          <Route path="/auth/signin" component={asyncAuth} />
          <Route path="/auth/signout" component={Logout} />
          <Route path="/" exact component={LandingPage} />
          <Redirect to="/dashboard" />
        </Switch>
      );
    }

    return (
      <div>
        <Layout>
          <br />
          {routes}
        </Layout>
      </div>
    );
  }
}

const mapStateToProps = state => {
  return {
    isAuthenticated: state.isAuthenticated,
  };
};

const mapDispatchToProps = dispatch => {
  return {
    onTryAutoSignup: () => dispatch( actions.authCheckState() ),
  };
};

export default withRouter( connect( mapStateToProps, mapDispatchToProps )( App ) );
