import React, { Component } from 'react';
import { connect } from 'react-redux';
import { withRouter } from 'react-router-dom';
import { Navbar, NavbarNav, NavItem, NavLink, NavbarToggler, Collapse, Dropdown, DropdownToggle, DropdownMenu,  DropdownItem, Fa } from "mdbreact";

class NavbarPage extends Component {
  state = {
    isOpen: true,
  };

  toggleCollapse = () => {
    this.setState({ isOpen: !this.state.isOpen });
  }

  render() {
    return (
        <Navbar color="default-color" dark expand="md">
          <NavbarToggler
            onClick={this.toggleCollapse}
          />
          <Collapse id="navbarCollapse3" isOpen={this.state.isOpen} navbar>
            <NavbarNav left>
              <NavItem>
                <NavLink to="/"><strong className="white-text">Experiment-X</strong></NavLink>
              </NavItem>
              <NavItem>
                <NavLink to="#!">Item1</NavLink>
              </NavItem>
              <NavItem>
                <NavLink to="#!">Item2</NavLink>
              </NavItem>
            </NavbarNav>
            <NavbarNav right>
              { !this.props.isAuthenticated ?
                  null :
                  <NavItem><NavLink to="/dashboard">Dashboard</NavLink></NavItem>
              }
              { !this.props.isAuthenticated ? 
                <NavItem><NavLink to="/auth/signin"><Fa icon="sign-in"/>Login</NavLink></NavItem> :
                <NavItem>
                  <Dropdown>
                    <DropdownToggle nav caret>
                      <Fa icon="user" />
                    </DropdownToggle>
                    <DropdownMenu className="dropdown-default" right>
                      <DropdownItem href="/profile">Profile</DropdownItem>
                      <DropdownItem href="#!">Settings</DropdownItem>
                      </DropdownMenu>
                    </Dropdown>
                  </NavItem>             
              }
              { !this.props.isAuthenticated ? 
                <NavItem><NavLink to="/auth/signup"><Fa icon="pencil"/>Register</NavLink></NavItem> :
                <NavItem><NavLink to="/auth/signout"><Fa icon="sign-out-alt"/>Logout</NavLink></NavItem>
              }
            </NavbarNav>
          </Collapse>
        </Navbar>
    );
  }
}

const mapStateToProps = state => {
  return {
    isAuthenticated: state.isAuthenticated
  };
};

export default withRouter(connect(mapStateToProps)(NavbarPage));